@import url(https://fonts.googleapis.com/css2?family=Nova+Slim&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0 auto;
  padding: 0;
  font-family: 'Nova Slim', cursive;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(63, 63, 63);
}

.App {
  width: 350px;
  background-image: linear-gradient(-225deg, #473B7B 0%, #3584A7 51%, #448ec0 100%);
  min-height: 100vh;
  /* box-shadow: 10px 5px 15px 5px #888888; */
}

.footer-container {
  display: flex;
  justify-content: center;
  width: 350px;
  height: 5%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  text-align: center;
  background-color: rgb(19, 57, 114);
  letter-spacing: 2px;
  font-weight: 600;
  color: white;
}

.chart-container {
  display: flex;
  justify-content: center;
  width: 85%;
  height: 150px;
  align-items: center;
  background-color: rgb(245, 245, 245);
  box-shadow: 1px 2px 10px 2px rgba(0, 0, 0, 5);
  border-radius: 10px;
  padding: 10px;
}
.dashboard-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

.balance-div {
    width: 90%;
    min-height: 190px;
}

.chart-div {
    width: 100%;
    min-height: 190px;
}

.transaction-div {
    margin-top: 10px;
    width: 100%;
}

.btn {
    position: relative;
    bottom: 230px;
    right: -140px;
    width: 10%;
    border-radius: 50%;
    padding: 10px;
    align-items: center;
    cursor: pointer;
    box-shadow: 1px 4px 3px 1px rgba(0, 0, 0, 3);
}
.balance-container {
    width: 100%;
    min-height: 180px;
    background-color: rgb(245, 245, 245);
    box-shadow: 1px 2px 10px 2px rgba(0, 0, 0, 5);
    border-radius: 10px;
}

.balance {
    margin: 0 auto;
    width: 50%;
    letter-spacing: 2px;
    padding: 5px;
    width: 90%;
}

.blancetext {
    color: white;
    padding: 10px;
    background-color: #528fec;
    border-radius: 5px;
    min-width: 90%;
    margin: 5px;
    min-height: 35px;
    text-shadow: 2px 2px #4b4b4b;
    text-transform: uppercase;
    font-weight: 600;
}

.chartdiv {
    width: 50%;
}

.blancetext span {}
    .inc-exp-container {
        height: 50%;
        letter-spacing: 2px;
        padding-top: 5px;
    }

    .income {
        color: white;
        padding: 10px;
        background-color: #389760;
        border-radius: 5px;
        min-width: 90%;
        margin: 5px;
        text-shadow: 2px 2px #4b4b4b;
        text-transform: uppercase;
        font-weight: 600;
        border: none;
    }

    .expense {
        color: white;
        padding: 10px;
        background-color: #ec9b4e;
        border-radius: 5px;
        min-width: 90%;
        margin: 5px;
        text-shadow: 2px 2px #4b4b4b;
        text-transform: uppercase;
    }

    span {
        float: right;
        font-weight: 600;
    }
.addtransactions-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 90%;
    background-color: rgb(245, 245, 245);
    align-items: center;
    text-align: center;
    border-radius: 10px;
    min-height: 200px;
}

.add-aransaction input {
    background-color: rgb(231, 231, 230);
    height: 35px;
    width: 210px;
    text-align: center;
    border-radius: 10px;
    box-shadow: inset 0px 0px 100px -55px rgba(0, 0, 0, 0.3);
    font-weight: 600;
    font-size: 15px;
}

button {
    border: none;
}

input {
    margin: 10px;
    border: none;
}

textarea:focus, input:focus {
    outline: none;
}

.buttons h5{
    color: red;
    font-size: 12px;
    font-weight: 600;
}

.incomesubmit {
    color: white;
    background-color: #389760;
    width: 80px;
    height: 50px;
    padding: 10px;
    border-radius: 10px;
    white-space: normal;
    word-wrap: break-word;
    font-weight: 600;
    margin: 5px;
    border: none;
    outline: none;
    box-shadow: 0px 0px 50px -55px rgba(211, 35, 35, 5);
    cursor: pointer;
}

.expensesubmit {
    color: white;
    background-color: #e68328;
    width: 80px;
    height: 50px;
    padding: 10px;
    border-radius: 10px;
    white-space: normal;
    word-wrap: break-word;
    font-weight: 600;
    margin: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}

.transactionlist-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    
}

.plus, .minus {
    display: flex;
    height: 6vh;
    text-align: center;
    align-items: center;
    border-radius: 10px;
}

.delete {
    width: 10%;
}

#id-transactions img {
    position: relative;
    width: 7%;
    cursor: pointer;
    left: 0px;
    
    padding: 2px;
    
}

.date {
    color: gray;
    font-size: 70%;
}

.minus {
    background-color: rgb(235, 213, 203);
    margin: 5px 10px 5px 10px;
}

.plus {
    background-color: rgb(173, 241, 167);
    margin: 5px 10px 5px 10px;
}

.textdate {
    display: flex;
    flex-direction: column;
    justify-items: left;
    align-items: left;
    text-align: left;
    align-content: left;
    

}

.textdate h5{
font-size: 18px;    
}
.profile-container {
    width: 40%;
    height: 75%;
    background-color: darkcyan;
}
.navbar-container {
    width: 100%;
    min-height: 140px;
}

.title {
    text-align: center;
    padding: 5px;
    background-color: rgb(1, 32, 80);
    letter-spacing: 3px;
    font-weight: 600;
    color: white;
    min-height: 50px;
    position: relative;
}

.title span {
    position: absolute;
    left: 0;
    right: 0;
    top: 15px;
    font-size: 25px;
}

.title img {
    padding: 10px;
    width: 10%;
    position: absolute;
    top: 0px;
    bottom: 5px;
    left: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.links {
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    text-align: center;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
}

#links:link {
    text-decoration: none;
}

#links:hover {
    background-color: rgb(226, 226, 226);
}

#links {
    position: relative;
}

#links h5 {
    position: absolute;
    color: black;
    font-weight: 600;
    bottom: 6px;
    right: 0;
    left: 0;
    
}

.dashboard-link, .addtransactions-link, .all-link, .profile-link {
    width: 75px;
    height: 75px;
    box-shadow: 1px 4px 3px 1px rgba(0, 0, 0, 0.2);
    padding: 2px;
    border-radius: 10px;
    background-color: rgb(245, 245, 245);
    margin: 10px 2px 10px 2px;
}

img {
    width: 50%;
}
.transaction-container {
    background-color: rgb(245, 245, 245);
    min-height: 400px;
    box-shadow: 1px 2px 10px 2px rgba(0, 0, 0, 5);
    border-radius: 10px;
    width: 90%;
    /* overflow-x: hidden; */
    overflow-y: scroll;
    max-height: 300px;
}

::-webkit-scrollbar {
    width: 10px;
    background-image: linear-gradient(-225deg, #5977db 0%, #3584A7 51%, #448ec0 100%);
    height: 10px;
    -webkit-transition: 1s;
    transition: 1s;
    border-radius: 30px;
}

::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.24);
    height: 1px;
    width: 10px;
}
